body {
  margin: 0;
  padding: 0;
  font-family: "Montserrat-Light", sans-serif;
  /* font-family: 'Monaco'; */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #F6F9FC;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1, h2 {
  font-family: 'Monaco';
}

@font-face {
  font-family: "Montserrat";
  font-weight: normal;
  src: local("Montserrat"),
  url(/JeromeJu.github.io/static/media/montserrat-regular.3fb4d290.woff) format("woff"),
  url(/JeromeJu.github.io/static/media/montserrat-regular.b94d4bb6.woff2) format("woff2");
}

@font-face {
  font-family: "Montserrat";
  font-weight: bold;
  src: local("Montserrat-Bold"),
  url(/JeromeJu.github.io/static/media/montserrat-bold.b760b19f.woff) format("woff"),
  url(/JeromeJu.github.io/static/media/montserrat-bold.45af3727.woff2) format("woff2");
}

@font-face {
  font-family: "Montserrat-Light";
  src: local("Montserrat-Light"),
  url(/JeromeJu.github.io/static/media/montserrat-light.a820606b.woff) format("woff"),
  url(/JeromeJu.github.io/static/media/montserrat-light.d0d8af75.woff2) format("woff2");
}

@font-face {
  font-family: "Inconsolata";
  font-weight: normal;
  src: local("Inconsolata"),
  url(/JeromeJu.github.io/static/media/Inconsolata-Regular.2c22fca2.ttf) format("ttf");
}

@font-face {
  font-family: "Inconsolata";
  font-weight: bold;
  src: local("Inconsolata-Bold"),
  url(/JeromeJu.github.io/static/media/Inconsolata-Bold.9af662a0.ttf) format("ttf");
}

@font-face {
  font-family: "Monaco";
  font-weight: normal;
  src: local("Monaco"),
  url(/JeromeJu.github.io/static/media/Monaco.5d922164.woff) format("woff");
}

@media only screen and (max-width: 900px) {
  body {
    display: block;
  }

  h2 {
    font-size: 18px;
    font-weight: bold;
  }

  h1 {
    font-size: 24px;
  }
}
.app-container {
  display: flex;
  flex-direction: column;
  margin: 0 80px;
}

@media only screen and (max-width: 900px) {
  .app-container {
    margin: 0 24px;
  }
}
.banner {
  display: flex;
  align-items: start;
  margin-top: 8%;
}

.portrait {
  margin: 0 0 25px;
  max-width: 180px;
  max-height: 180px;
  border-radius: 999px;
}

.image-links {
  display: flex;
  flex-direction: column;
}

.typed-text {
  margin-top: 24px;
}

.name {
  line-height: 138%;
  margin: 0 64px;
  font-weight: 70;
  font-size: 30px;
}

@media only screen and (max-width: 900px) {
  .banner {
    flex-direction: column;
    align-items: center;
  }

  .typed-text {
    width: 100%;
  }
  
  .space-holder {
    height: 28px;
  }
}

.typewriter {
  /* display: flex; */
  /* justify-content: flex-start; */
  /* margin: 0 64px; */
  /* color: #5A5D7A; */
  font-family: 'Monaco';
  font-weight: 70;
  /* font-family: monospace; */
  font-size: 18px;
  pointer-events: none;
}

.typewriter span {
  height: 28px;
  width: 0px;
  /* margin: 8px 2px 0; */
  background-color: #00000000;
  -webkit-animation: blink 1s steps(1);
          animation: blink 1s steps(1);
}

@-webkit-keyframes blink {
  50% {
    background-color: transparent;
  }
}

@keyframes blink {
  50% {
    background-color: transparent;
  }
}

@media only screen and (max-width: 900px) {
  .typewriter {
    font-size: 24px;
    margin: 0;
  }

  .typewriter span {
    height: 28px;
    width: 18px;
    margin: 0 4px;
  }
}
.experience {
  display: flex;
  flex-direction: column;
  /* width: 1100px; */
  margin-top: 24px;
}
.experience-item {
  max-width: 1100px;
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  margin-bottom: 60px;
  background-color: #FFFFFF;
  border-radius: 4px;
  box-shadow: 0 50px 100px rgba(50, 50, 93, 0.05),
      0 15px 35px rgba(50, 50, 93, 0.1), 0 5px 15px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  /* transition: transform 0.2s ease-in; */
}

/* .experience-item:hover {
  transform: scale(1.01);
  -webkit-transform: scale(1.01);
} */

.experience-background {
  position: absolute;
  top: -1px;
  right: -1px;
  height: 100%;
  width: 80%;
  background: url(/JeromeJu.github.io/static/media/vectorpaint.7b98bf11.svg);
  background-size: cover;
}

.experience-image {
  width: 40%;
  position: absolute;
  top: 50%;
  left: 0;
  -webkit-transform: translate(0, -50%);
          transform: translate(0, -50%);
  transition: all 0.07s ease-in;
}

.experience-image:hover {
  opacity: 0.85;
}


.experience-content {
  z-index: 2;
  min-height: 240px;
  width: 56%;
  padding: 48px;
  line-height: 1.6;
}

.experience-content ul {
  padding: 0;
  list-style-type: circle;
  list-style-position: outside;
}

.company {
  display: none;
}

.image-small {
  display: none;
  height: 48px;
}

.position {
  margin: 0;
}

.team {
  margin: 0;
  font-style: italic;
}

.date {
  font-style: italic;
  font-size: 0.9em;
}

.description {
  margin-bottom: 8px;
}

@media only screen and (max-width: 750px) {
  .experience-image, .experience-background {
    display: none;
  }

  .experience-content {
    min-height: 0;
  }

  .image-small {
    display: block;
    margin-bottom: 16px;
  }

  .position {
    font-size: 1em;
  }

  .team {
    font-size: 0.9em;
  }

  .experience-content {
    width: 100%;
    padding: 32px;
  }
}
.skills-content {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

@media only screen and (max-width: 700px) {
  .skills-content {
    flex-direction: column;
  }
}
.skill-item {
  display: flex;
  flex-direction: column;
}

.skill-item:last-child {
  margin: 0;
}

.layer-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.each-skill {
  display: flex;
  flex-direction: column;
}

.each-skill p {
  margin: 8px 0;
}

.layer {
  font-family: 'Monaco';
}

.logo {
  width: 100px;
}

@media only screen and (max-width: 700px) {
  .skill-item {
    flex-direction: row;
    margin-bottom: 24px;
  }

  .each-skill {
    flex-direction: column;
    flex-wrap: wrap;
    height: 120px;
  }

  .each-skill p {
    margin: 8px 16px;
  }

  .layer {
    font-size: 1em;
  }
}
.footer {
  text-align: center;
  margin: 40px;
}

.footer div {
  padding: 8px;
}
